.addcandidatepage{
    display: flex;
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;

}
.addcandidatepage>div{
    flex: 1;
}
.addcandidate{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    /* align-items: center; */
}
.addcandidate h1{
    text-align: center;
    color: #6495ED;
    margin-top: 200px;
    margin-bottom: 20px;
}
.addcandidateimg img{
    width: 100%;
    height: 100%;
}

@media(max-width:500px){
    .addcandidatepage{
        flex-direction: column;
    }
    .addcandidate h1{
        margin-top: 50px;
    }
    .formm{
        padding: 10px;
    }
    
}