*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.homepage{
    width: 100%;
    height: 100%;
    max-height: 100% !important;
}
.home{
    display: flex;
    height: 100%;
    max-height: 100% !important;
    /* width: 100% !important;
    height: 100% !important; */
}
.home>div{
    flex:1;
    display: flex;
    align-items: center;
    height: 100vh !important;
    min-height: 100% !important;
    max-height: 100% !important;
}
.homeimg img{
    width: 100%;
    height: 100%;
}
.welcome{
    flex-direction: column;
    color: white;
    background: linear-gradient(to left,#FFFFFF,rgba(89, 124, 168, 0.3),rgba(55, 116, 166, 0.4),rgba(41, 94, 168, 0.8))!important;
}
.welcome h1{
    font-size: 50px;
    text-align: center;
    margin-top: 200px!important;
    margin-bottom: 10px;
}
.btn{
    width: fit-content;
    padding: 10px;
    font-size: 15px;
    font-weight: 600;
    margin: 20px 0px;
    height: 50px;
    border: none;
    box-shadow: 0px 4px 5px 0px #2C6EDF, 0px 5px 7px 0px #2C6EDF;
    color: white;
    cursor: pointer;
    border-radius: 20px;
    background-color: #2C6EDF;
}
.spinner{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    /* margin:200px; */
    /* text-align: center; */
}
button:disabled{
    opacity: 0.8;
    cursor:no-drop;
}
@media(max-width:500px)
{
    .home{
        flex-direction: column;
    }
    .welcome h1{
        margin-top: 50px !important;
        font-size: 30px;
    }
    .welcome{
        padding-bottom: 100px;
    background: linear-gradient(360deg,#FFFFFF,rgba(89, 124, 168, 0.3),rgba(55, 116, 166, 0.4),rgba(41, 94, 168, 0.8))!important;
    }

}