*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.electionspage{
    width:100%;
    max-width: 100% !important;
    overflow: hidden;
}
.createelection{
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
}
.createelection h1{
    margin-bottom: 20px;
    color: #6495ED;
}
.electiondiv{
    width: 100%;
    max-width: 100%;
    height: 300px;
    overflow-y: scroll;
    padding: 10px;
    /* overflow: hidden; */
    /* background-color: aqua; */
}
.election{
    display: flex;
    margin-bottom: 10px;
    padding: 15px 20px 15px 20px;
    align-items: center;
    justify-content: space-between;
    background-color:#6495ED;
    font-size: 18px;
    /* font-weight: bold; */
    color: white;
    box-shadow: 1px 3px 4px 2px  rgba(0, 0, 0,0.2),2px 4px 5px 3px rgba(0, 0, 0,0.2);
}
.electionid{
    margin-right: 5px;
}
.totalvotes{
justify-content: flex-end !important;
}
.electiondetailspage{
    padding: 10px 10px 0px 15px;
    display: flex;
    flex-direction: column;
}
.electiondetails{
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    /* padding: 10px 10px 0px 15px; */
    /* background-color: aqua; */
}
.electiondetailspage h3{
    margin: 20px 0px;
}
.electiondetails>div{
    margin-right: 20px;
}
.eleclabel{
    font-size: 20px;
    font-weight: bold;
    color: #6495ED;
}
.candidates{
    display: flex;
}
.elecdet
{
    display: flex;
}
.elecval{
    font-size: 18px;
    font-weight: bold;
}
.elecdet>div{
    margin-right: 20px;
}
.candidatedetails{
    display: flex;
    flex-direction: column;
    width: 400px;
    max-width: 400px;
    /* background-color: pink; */
}
.candlabel{
    color: #6495ED;
    font-weight: bold;
}
.candval{
    color: black;
    font-weight: normal;
}
.candidatedetails p{
    margin-bottom: 10px;
    
}
.candidate{
    margin-right: 25px;
    border-left: 2px solid #6495ED;
    display: flex;
    align-items: flex-start;
    padding: 10px 20px 10px 20px;
    border-radius: 10px;
    box-shadow: 1px 3px 4px 2px  rgba(0, 0, 0,0.2),2px 4px 5px 3px rgba(0, 0, 0,0.2);
}
.startbtn,.endbtn,.votebtn{
    width: fit-content;
    padding: 10px;
    font-size: 15px;
    /* font-weight: 600; */
    margin: 20px 0px;
    height: 50px;
    border: none;
    /* box-shadow: 0px 4px 5px 0px #2C6EDF, 0px 5px 7px 0px #2C6EDF; */
    color: white;
    cursor: pointer;
    border-radius: 20px;
    background-color:red;
}
.startbtn,.votebtn{
    background-color: rgb(21, 167, 21);
}
.radiobtn{
    margin-top: 5px;
    margin-right: 2px;
}
.votebtn{
    margin: 0px;
    width: 100px;
    min-width: 100px;
}
.btndiv{
    display: flex;
    align-self: flex-end;
    margin-left: 80%;
}
.btndiv button{
    margin-right: 10px;

}
